import Style from "ol/style/Style";
import Circle from "ol/style/Circle"
import Fill from "ol/style/Fill";

var redStyle = new Style({
    image: new Circle({
        radius: 10,
        fill: new Fill({
            color:'#dc143c'
        })
    })
});

export var fillRedToFeature=function(feature){
    feature.setStyle(redStyle);
}